import React from 'react';
import CompressionSection from '../../Component/CompressionSection/CompressionSection';
import AppSideBar from '../../Component/AppSiderbar/AppSideBar';
import AppHeader from '../../Component/AppHeader/AppHeader';
import AddLibrary from '../../Component/AddLibrary';

const Compression = () => {
  return (
    <div>
      <div>
        {AddLibrary(
          'https://demo.dashboardpack.com/architectui-html-free/assets/scripts/main.js')}
        <div class="app-container app-theme-white body-tabs-shadow fixed-sidebar fixed-header">
          <AppHeader></AppHeader>
          <div class="app-main">
            <AppSideBar></AppSideBar>
            <div class="app-main__outer">
              <CompressionSection></CompressionSection>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Compression;