import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import './Dashboard.css';
import MianInner from '../../Component/Main-Inner-dasboard/MianInner';
import AppHeader from "../../Component/AppHeader/AppHeader";
import AppSideBar from "../../Component/AppSiderbar/AppSideBar";
import AddLibrary from "../../Component/AddLibrary";
import axios from "axios";

const Dashboard = () => {

  const Data = useSelector(state => state.Data)

  useEffect(() => {
    console.log("login api data", Data.token);
    axios.get(`https://qb.flitsync.com/api/dashboard.php`, {
      headers: {
        "Authorization": `Bearer ${Data.token}`
      }
    })
      .then(res => {
        console.log("Dashboard Data api",res.data);
      })
      .catch(error => {
        alert(error)
        console.warn("Dashboard api error", error)
      })
      
  }, [])

  return (
    <div>
      {AddLibrary(
        'https://demo.dashboardpack.com/architectui-html-free/assets/scripts/main.js')}
      <div class="app-container app-theme-white body-tabs-shadow fixed-sidebar fixed-header">
        <AppHeader></AppHeader>
        <div class="app-main">
          <AppSideBar></AppSideBar>
          <div class="app-main__outer">
            <MianInner></MianInner>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Dashboard