import React, { useState, useEffect } from 'react'
import axios, { all } from 'axios';
import Select from 'react-select';
import AddLibrary from '../AddLibrary';
import { useSelector } from "react-redux";
import Moment from 'moment';

function CompressionSection() {

    const Data = useSelector(state => state.Data)
    const [alldata, setalldata] = useState([])
    const [prealldata, setprealldata] = useState([])
    const [month, setmonth] = useState(null);
    const [year, setyear] = useState(null)
    const [premonth, setpremonth] = useState(null)
    const [preyear, setpreyear] = useState(null)

    useEffect(() => {
        var year = Moment().format('YYYY')
        var month = Moment().format('MM')
        axios.get(`https://qb.flitsync.com/api/salesdata.php?month=${month}&year=${year}`, {
            headers: {
                "Authorization": `Bearer ${Data.token}`
            }
        })
            .then(({ data, status }) => {
                console.log("salesdata api", data);
                setalldata(data)
            })
            .catch(error => {
                alert(error)
                console.warn("salesdata api error", error.response.data)
            })

        var preyear = Moment().subtract(1, 'years').format('YYYY')
        var month = Moment().format('MM')
        axios.get(`https://qb.flitsync.com/api/salesdata.php?month=${month}&year=${preyear}`, {
            headers: {
                "Authorization": `Bearer ${Data.token}`
            }
        })
            .then(({ data, status }) => {
                console.log("pre year salesdata api", data);
                setprealldata(data)
            })
            .catch(error => {
                alert(error)
                console.warn("pre year salesdata api error", error.response.data)
            })
    }, [])

    const options = [
        { value: '01', label: 'Jan' },
        { value: '02', label: 'Feb' },
        { value: '03', label: 'Mar' },
        { value: '04', label: 'Apr' },
        { value: '05', label: 'May' },
        { value: '06', label: 'Jun' },
        { value: '07', label: 'Jul' },
        { value: '08', label: 'Aug' },
        { value: '09', label: 'Sep' },
        { value: '10', label: 'Oct' },
        { value: '11', label: 'Nov' },
        { value: '12', label: 'Dec' },
    ];

    const OPtions1 = [
        { value: '2023', label: '2023' },
        { value: '2022', label: '2022' },
        { value: '2021', label: '2021' },
        { value: '2020', label: '2020' },
        { value: '2019', label: '2019' },
        { value: '2018', label: '2018' },
        { value: '2017', label: '2017' },
        { value: '2016', label: '2016' },
        { value: '2015', label: '2015' },
    ]

    const HandleData = () => {
        axios.get(`https://qb.flitsync.com/api/salesdata.php?month=${month}&year=${year}`, {
            headers: {
                "Authorization": `Bearer ${Data.token}`
            }
        })
            .then(({ data, status }) => {
                console.log("salesdata api", data);
                setalldata(data)
            })
            .catch(error => {
                alert(error)
                console.warn("salesdata api error", error.response.data)
            })
    }

    const PreHandleData = () => {
        axios.get(`https://qb.flitsync.com/api/salesdata.php?month=${premonth}&year=${preyear}`, {
            headers: {
                "Authorization": `Bearer ${Data.token}`
            }
        })
            .then(({ data, status }) => {
                console.log("salesdata api", data);
                setalldata(data)
            })
            .catch(error => {
                alert(error)
                console.warn("salesdata api error", error.response.data)
            })
    }

    return (
        <div>
            <div className='InnerSpace Compresssion'>
                <div className='row'>
                    <div class="col-md-12">
                        <div class="main-card mb-3 card">
                            <div class="card-header">Compression

                            </div>

                            <div className='row'>
                                <div class="col-xl-5 col-lg-12 col-md-12 col-12"><div className='card01'>
                                    <div className='col'>
                                        <div class="table-responsive ">
                                            <table class="align-middle mb-0 table table-borderless table-striped table-hover">
                                                <thead>
                                                    <tr>
                                                        <th style={{ width: "40%" }} class="text-center">
                                                            <div className='SelectedNav'>
                                                                <div> Year</div>
                                                                <Select
                                                                    defaultValue={year}
                                                                    onChange={(year) => {
                                                                        setyear(year.value)
                                                                        HandleData()
                                                                    }}
                                                                    options={OPtions1}
                                                                // isMulti={true}
                                                                />
                                                            </div>

                                                        </th>
                                                        <th style={{ width: "60%" }} class="text-center">
                                                            <div className='SelectedNav'>
                                                                <div>Month </div>
                                                                <Select
                                                                    defaultValue={month}
                                                                    onChange={(month) => {
                                                                        setmonth(month.value)
                                                                        HandleData()
                                                                    }}
                                                                    options={options}
                                                                // isMulti={true}
                                                                />

                                                            </div>

                                                        </th>

                                                    </tr>
                                                </thead>

                                                <tbody>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Bookings :</b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b> Total Sale Product :</b> </td>
                                                        <td class=" ">
                                                            {alldata.total_count}
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Subtotal :</b> </td>
                                                        <td class=" ">
                                                            {alldata.total_Sub_amount}
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Tax : </b></td>
                                                        <td class=" ">
                                                            {alldata.total_tax_amount}
                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Shipping : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Discounts : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Sale Price/Revenue : </b></td>
                                                        <td class=" ">
                                                            {alldata.total_amount}
                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Customers : </b></td>
                                                        <td class=" ">
                                                            {alldata.total_unique_customers}
                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Customer addon : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Most purchased sale Product  : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Most higher price sale product : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Most Lowest price sale product : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                </tbody>
                                            </table>

                                        </div>

                                    </div>
                                </div>
                                </div>
                                <div class="col-xl-2 col-lg-12 col-md-12 col-12 height100">
                                    <div class="center-div">
                                        <div className='ArroWs'>
                                            <div>&rarr;</div>
                                            <div>&larr;</div>
                                        </div>

                                    </div>
                                </div>

                                <div class="col-xl-5 col-lg-12 col-md-12 col-12"><div className='card01'>
                                    <div className='col '>
                                        <div class="table-responsive">
                                            <table class="align-middle mb-0 table table-borderless table-striped table-hover">
                                                <thead>
                                                    <tr>
                                                        <th style={{ width: "40%" }} class="text-center">
                                                            <div className='SelectedNav'>
                                                                <div>Year</div>
                                                                <Select
                                                                    defaultValue={month}
                                                                    onChange={(month) => {
                                                                        setpreyear(month.value)
                                                                        PreHandleData()
                                                                    }}
                                                                    options={OPtions1}
                                                                // isMulti={true}
                                                                />

                                                            </div>

                                                        </th>
                                                        <th style={{ width: "60%" }} class="text-center" >
                                                            <div className='SelectedNav'>
                                                                <div>Month </div>
                                                                <Select
                                                                    defaultValue={month}
                                                                    onChange={(month) => {
                                                                        setpremonth(month.value)
                                                                        PreHandleData()
                                                                    }}
                                                                    options={options}
                                                                // isMulti={true}
                                                                />

                                                            </div>

                                                        </th>

                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Bookings :</b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b> Total Sale Product :</b> </td>
                                                        <td class=" ">
                                                            {prealldata.total_count}
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Subtotal :</b> </td>
                                                        <td class=" ">
                                                            {prealldata.total_Sub_amount}
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Tax : </b></td>
                                                        <td class=" ">
                                                            {prealldata.total_tax_amount}
                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Shipping : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Discounts : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Sale Price/Revenue : </b></td>
                                                        <td class=" ">
                                                            {prealldata.total_amount}
                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Customers : </b></td>
                                                        <td class=" ">
                                                            {prealldata.total_unique_customers}
                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Total Customer addon : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Most purchased sale Product  : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Most higher price sale product : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                    <tr>
                                                        <td class=" text-muted"><b>Most Lowest price sale product : </b></td>
                                                        <td class=" ">

                                                        </td>

                                                    </tr>
                                                </tbody>
                                            </table>

                                        </div>

                                    </div>
                                </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>

            </div>
        </div>
    )
}

export default CompressionSection